.App{
  width: 90%;
  padding: 2%;
  gap: 5%;
}
.confirm-dialog {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.confirm-dialog-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

.confirm-dialog-content button {
  margin: 5px;
  border-radius: 5px;
  border: none;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  padding: 5%;
}
.confirm-dialog-content-yes{
  background-color: rgb(255,0,0);
  border: 1px solid rgb(111, 39, 39);
}
.confirm-dialog-content-no{
  background-color: rgb(0,255,68);
  border: 1px solid rgb(46, 133, 46);
}
.confirm-dialog-content button:hover{
  transition: all 0.3s ease-in-out;
  padding: 7%;
}
.message-box {
  position: fixed;
  top: 10px;
  left: -300px; /* Start outside the viewport on the left */
  padding: 25px;
  border-radius: 8px;
  font-size: 20px;
  color: white;
  z-index: 1000;
  transition: opacity 0.5s ease-in-out;
}

.message-box.success {
  background-color: green;
}

.message-box.error {
  background-color: red;
}

.message-box.show {
  animation: slide-in 0.5s forwards;
}

.message-box.hide {
  animation: slide-out 0.5s forwards;
}

@keyframes slide-in {
  from {
      left: -300px;
      opacity: 0;
  }
  to {
      left: 10px;
      opacity: 1;
  }
}

@keyframes slide-out {
  from {
      left: 10px;
      opacity: 1;
  }
  to {
      left: -300px;
      opacity: 0;
  }
}

body{
  background-color: rgb(245,255,255);
}
.adduser{
  display: flex;
  flex-direction: column;
  background-color: rgb(109,215,253);
  padding: 3%;
  border-radius: 8px;
  height: auto;
  width: 50%;
  box-shadow: 1px 2px 2px 1px rgb(223, 223, 223);
  transition: all 0.3s ease-in-out;
}
.adduser:hover{
  border: 2px solid rgb(0,147,203);
  transition: all 0.5s ease-in-out;
  box-shadow: 1px 2px 20px 1px rgb(0,147,203);

}
.adduser input{
  margin-bottom: 1%;
  border-radius: 5px;
  border: 1px solid rgb(0,147,203);
  padding: 1%;
  outline: 1px solid rgb(0,90,205);
  transition: all 0.3s ease-in-out;
  box-shadow: 1px 2px 2px 1px rgb(42, 71, 216)
}
.adduser input:hover{
  transition: all 0.3s ease-in-out;
  padding-bottom: 3%;
}
.adduser button{
  margin-top: 3%;
  border-radius: 5px;
  border: 1px solid rgb(0,147,203);
  background-color: rgb(190,240,255);
  font-size: 20px;
  color: black;
  cursor: pointer;
  padding: 1%;
  transition: all 0.3s ease-in-out;
}
.adduser button:hover{
  transition: all 0.3s ease-in-out;
  background-color: rgb(0,147,203);
}
.adduser label{
  font-size: 17px;
  font-weight: 600;
  margin-bottom: 2%;
  margin-top: 2%;
}
.card-boxs{
  display: flex;
  width: 100%;
  flex-basis: 33%;
  flex-wrap: wrap;
  gap: 5%;
}
.card-boxs span{
  display: flex;
  width: 100%;
  justify-content: flex-end;
  gap: 7%;
}
.card-boxs span img{
  width: 30px;
  height: 30px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.card-boxs span img:hover{
  animation: vibrate 0.3s linear infinite;
}
@keyframes vibrate {
  0% { transform: translate(0); }
  20% { transform: translate(-1.5px, 1.5px); }
  40% { transform: translate(-1.5px, -1.5px); }
  60% { transform: translate(1.5px, 1.5px); }
  80% { transform: translate(1.5px, -1.5px); }
  100% { transform: translate(0); }
}

.cards{
  width: 25%;
  margin-top: 3%;
  display: flex;
  flex-direction: column;
  height: auto;
  box-shadow: 1px 2px 4px 2px rgb(109,215,253);
  border-radius: 8px;
  padding: 2%;
  transition: all 0.3s ease-in-out;
  background-color: rgb(190,240,255);
}
.cards:hover{
  transition: all 0.3s ease-in-out;
  box-shadow: 1px 2px 6px 3px rgb(109,215,253);
  border: 1px solid rgba(0,147,203,0.6);

}
.cards h1{
  font-size: 20px;
  display: flex;
  align-items: center;
}
.cards h1 img{
